import React, { MouseEventHandler } from 'react';
import './ButtonComponent.scss';
import { BaseIconComponent } from '../icons/base/base-icon';

interface ButtonComponentProps {
  label: string;
  state?: 'primary' | 'secondary';
  theme: 'light-theme' | 'dark-theme';
  disabled?: boolean;
  showIcon?: boolean;
  showText?: boolean;
  onAction?: MouseEventHandler<HTMLButtonElement>;
}

const ButtonComponent: React.FC<ButtonComponentProps> = ({
  label,
  state = 'secondary',
  theme,
  disabled = false,
  showIcon = true,
  showText = true,
  onAction,
}) => {
  // Generate classes based on state, theme, and disabled status
  const classes = [
    `button--${state}`,
    `button--${state}--${theme}`,
    disabled ? `button--disabled--${state}` : '',
    disabled ? `button--disabled--${state}--${theme}` : '',
  ].filter(Boolean).join(' ');

  // Click event handler
  const handleClick = (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    if (onAction) {
      onAction(event);
    }
  };

  return (
    <button
      className={`button ${classes}`}
      onClick={handleClick}
      disabled={disabled}
    >
      <div className="label-container">
        <div className="label">
          {showIcon && <BaseIconComponent name="add" size="base" theme={theme} />}
          {showText && <span>{label}</span>}
        </div>
      </div>
    </button>
  );
};

export default ButtonComponent;
